@charset "utf-8";
*{margin: 0; padding: 0;}
li{list-style-type: none;}
a{text-decoration: none; color: inherit;}
small{font-size: 12px;}
.inner{width: 1200px; margin: 0 auto;}
body{font: 12px '돋움',dotum,sans-serif; color: #333;}
// .test{outline: 1px dotted red;}

html{
  background: url(../images/bg1.jpg) no-repeat center /cover fixed;
/*   background-size: cover; */
}

#header{
  background-color: #fff; opacity: .5;
  
  .innerheader{
    max-width: 1200px; margin: 0 auto;
    height: 100px;
  }
  
  h1{
      color: black;
      display: inline-block;
      line-height: 100px;
  }
  
  ul{
    line-height: 100px;
    display: flex;
    float: right;

    li{
      padding: 0 5px;
      position: relative;
      &:nth-child(1)::after{
        content: "|"; position: absolute; right: -3px; top: 0;
      }
      &:last-child{
        padding-right: 0;
      }
      
      a{
        transition: 0.2s;
      }
      
      a:hover{
        text-decoration: underline;
      }
    }
  }
}

#container{
  position: fixed; left: 50%; top: 60%; transform: translate(-50%);
  .btn{
    /*   position: fixed; left: 50%; top: 50%;
      transform: translate(-50%, -50%); */
      text-align: center;
    a{
      display: inline-block;
      width: 120px; padding: 10px 0;
      text-align: center; text-transform: uppercase; border-radius: 5px; border: 1px solid #fff; color: #fff;
      transition: 0.3s;
      &:hover{
        background-color: #ffffffca; color: black;
      }
    }
  }
}

#footer{ 
  position: fixed; bottom: 0; width: 100%; color: #fff;
  .innerfooter{
    margin: 0 auto; max-width: 1200px;
    padding: 50px 0;
  }
  small{

  }
}


